@import url(https://fonts.googleapis.com/css2?family=Palanquin:wght@200;300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #FDFDFD;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.header {
  width: 100%;
  background-color: #1DC9FF;
  height: 70px;
  display: flex;
  justify-content: center;

}

.list-point {
  color: white;
  font-size: 20px;
  text-decoration: none;
}

.list {
  padding-top: 20px;
  width: 400px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.index-wrapper {
    font-weight: 500;
    font-family: 'Palanquin', sans-serif;
    padding-top: 50px;
    padding-left: 120px;
    padding-right: 120px;
    font-size: 36px;
}
.index-title {

    justify-self: start;
    padding-bottom: 50px;
}

.wares {
    width: auto;
    display: grid;
    grid-template: "a b c d";
    justify-content: space-between;
}

.ware-wrapper {
    display: grid;
    grid-template-rows: 55fr 45fr;
    width: 260px;
    min-height: 320px;
    box-shadow: 0px 4px 11px rgba(0, 0, 0, 0.11);
    border-radius: 30px;
}

.ware-wrapper:first-of-type{
    justify-self: start;
}

.image-wrap {
    box-sizing: content-box;
    background-color: #F8F8F8;
    height: 100%;
    width: 100%;
    border-radius: 30px 30px 0 0;
    display: flex;
    justify-content: center;
    padding-top: 15px;
}

.ocean {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    color:white;
    background: linear-gradient(82.95deg, rgb(124, 222, 252) -5.23%, #1DC9FF 96.65%);
    padding: 20px 20px;
    padding-left: 25px;
    border-radius: 0 0 30px 30px;
}

.ware-title {
    font-weight: 600;
    font-family: 'Palanquin', sans-serif;
    font-size: 22px;
    line-height: 130%;
}

.ware-text {
    font-family: 'Palanquin', sans-serif;
    font-size: 14px;
}

.ware-image {
    height: 150px;
    width: 150px;
    object-fit: cover;
}
.button-flex {
    font-family: 'Palanquin', sans-serif;
    width: 100%;
    display: flex;
    justify-content: space-between;
    font-size: 22px;
    align-items: flex-end;
}
.addbutton {
    font-weight: 500;
    line-height: 140%;
    font-family: 'Palanquin', sans-serif;
    -webkit-transition: all 0.3s;
    font-size: 15px;
    border: 3px solid #FFFFFF;
    box-sizing: border-box;
    box-shadow: 0px 0px 4px rgba(255, 255, 255, 0.82);
    border-radius: 10px;
    padding: 5px 20px;
    color: white;
    background-color: transparent;
    cursor: pointer;
    width: 120px;
}

.addbutton:hover {
    background-color: rgb(248, 187, 73);
}

.addbutton:active {
    outline:none;
    transform: translateY(2px);
}
.addbutton:focus {
    outline: none;
}


.readbutton {
    margin-left: 40px;
    border-radius: 5px;
    padding: 10px 20px;
    border: 2px solid teal;
    background-color: transparent;
    color: teal;
    opacity: 0.7;
}

.pricediv {
    font-family: 'Palanquin', sans-serif;
    font-size: 22px;
    width: 100px;
    margin-right: 0;
}

.buttonflex {
    align-items: center;
    display: flex;
}

.plusminusbutton {
    font-family: 'Palanquin', sans-serif;
    -webkit-transition: all 0.3s;
    font-size: 20px;
    border: 3px solid #FFFFFF;
    box-sizing: border-box;
    box-shadow: 0px 0px 4px rgba(255, 255, 255, 0.82);
    border-radius: 10px;
    color: white;
    background-color: transparent;
    cursor: pointer;
    display: grid;
    width: 120px;
    align-items: center;
    grid-template-columns: 40fr 40fr 40fr;


}

.plusminusbutton:active {
    outline:none;
    transform: translateY(2px);
}

.iter {
    -webkit-transition: all 0.1s;
    height: 12px;
    width: 12px;
}

.iter-div {
    -webkit-transition: all 0.1s;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
}

.iter-div:hover {
    border-radius: 10px;
    background-color: rgb(248, 187, 73);
    transform: scale(1.1);
}

.ware-amount {
    line-height: 25px;
    text-align: center;
    padding-bottom: 4px;
}

.cart-wrap {
    display: grid;
    padding-left: 12vw;
    padding-right: 12vw;
    grid-template: "1 2";
    justify-content: space-between;
    grid-row-gap: 50px;
    row-gap: 50px;
}



.cart-item {
    display: grid;
    grid-template-columns: 26fr 74fr;
    box-shadow: 0px 4px 11px rgba(0, 0, 0, 0.11);
    border-radius: 30px 30px 0px 30px;
    width: 33vw;
    min-width: 450px;
    min-height: 150px;
}

.cart-info {
    font-family: 'Palanquin', sans-serif;
    display: grid;
    grid-template: "a"
                    "b";
    align-content:space-between ;
    background: linear-gradient(69.15deg, #96E6FF -5.23%, #1DC9FF 96.65%);
    border-radius: 0px 30px 0px 0px;
    padding-top: 20px;
    padding-bottom: 25px;
    padding-right: 30px;
    padding-left: 30px;
    color: white;
}
.cart-image-wrap {
    align-items: center;
    justify-content: center;
    display: flex;
    height: auto;
    width: 130px;
    background: #F8F8F8;
    border-radius: 30px 0px 0px 30px;
}

.cart-image {
    height: 90px;
    width: 100px;
    object-fit: cover;
}

.cart-title {
    font-family: 'Palanquin', sans-serif;
    font-size: 30px;
    padding-top: 50px;
    grid-column-start: 1;
    grid-column-end: 3;
}

.cart-bottom-flex {
    display: flex;
    justify-content: space-between;
}

.cart-info-title {
    font-size: 25px;
}

.amount-span {
    font-size: 25px;
    padding-left: 20px;
    padding-right: 20px;
}

.adder {
    display: flex;
    align-items: center;
}

.checkout-wrap {
    grid-column-start: 1;
    grid-column-end: 3;
    display: flex;
}

.calc-span {
    font-size: 25px;
}

.increm:disabled {
    opacity: 0.5;
    pointer-events: none;
}

.increm {
    border: none;
    background-color: transparent;
    display: flex;
    align-items: baseline;
}

.increm:focus {
    border: none;
    outline: 0
}


